export default {
	data() {
		return {
			setting:[
					{
						id:0,
						title:"新消息通知",
						info:"接收消息",
						onoff:true
					},
					/* {
						id:1,
						title:"图片加载",
					}, */
					{
						id:1,
						title:"修改密码",
						linkUrl:"../changepwd/changepwd"
					},
					{
						id:2,
						title:"清除缓存",
						info:"清除成功"
					},
					/* {
						id:4,
						title:"退出登录",
					} */				
				]	
			}
	},
	onLoad() {
		
	},
	methods: {
		itemClick(e){
			let id = e.currentTarget.id.toString().substr(3);
			
			if (id == 0 || id == 2) {
				if (id == 0) {
					
				}else{
					console.log(id);
					uni.showToast({
						title: this.setting[id].info,
						icon:"none",
						duration: 500
					});
				}
			}
			if (id == 1) {
				uni.navigateTo({
				    url: this.setting[id].linkUrl
				});
			}
		},
		msgChange(e){
		    let isSelect = e.detail.value;
			let title = "开启消息通知";
			if(!isSelect){
				title="关闭消息通知";
			}
			/* this.setting[0].onoff = !this.setting[0].onoff;  
			var self = this;  
			                setTimeout(function() {  
			                    self.setting[0].onoff = false;  
			                }, 2000) */ 
			uni.showToast({
				title: title,
				icon:"none",
				duration: 500
			});
		}
	}
}
